            {{ displayProfit(item.symbol, item.direction, item.strike, item.amount, item.optionPrice) !== '0' ? language.win : language.lose }}
          </b>


            <img  class="win-text" :src="item.direction === 0 ? downImage : upImage" alt="">

            <span class="c1">{{ language.type }}<br><b class="text">{{ item.symbol }}</b></span>
            <span class="c2">P&L<br><b class="text">{{ item.PL }}</b></span>
            <span class="c520">

              <div class="my-spna-div" v-if="tabIndex == 1">{{language.purchaseDays}}</div>
              
              <div class="my-spna-div" v-else>
                          <span  v-if="item.countdownTime <= 0">{{language.overdue}}</span> 
                          <span  v-else>{{language.countdown}}</span> 
                </div> 


              <div class="text text-nowrap">
                {{ item.displayCountdown }}
              </div>
            </span>
          </div>

            <p class="base-info flex jc-between">
              <span class="s1">
               {{language.aoption_lp_amount}}<br>
                <b class="text fs-20">{{ item.amount }} {{ item.symbol }}</b>
              </span>
              <span class="s3 ta-right">
                {{language.aoption_trade_amount}}<br>$
                <span class="text" v-if="item.optionPrice">{{ item.optionPrice.substr(0,10) }}</span>
              </span>
            </p>
            <div v-show="true" class="res-box flex jc-between ai-center border-gray hdr-el-gray"
              :class="{'border-green hdr-el-green': true,'border-red hdr-el-red': false}"
            >
              <span class="s1 "  :class="listeningLanguage?'':'s120'">
                {{language.aoption_trade_price}}($)<br>
                <b class="fs-20 text" >{{item.displayStrike}}</b>
              </span>
              <span class=" s100"  >
