          </div>
        </li>
      </ul>

      <ul  class="my-ul-box"  v-show="!loading&&transactionList.length===0">
          <li class="no-data flex jc-center ai-center" >
          <p class="text fs-16 ta-center">
            <img src="@/images/no-data.png" alt="nodata"><br>{{ language.no_data }}
          </p>
        </li>
      </ul>

      <div v-if="loading" class="mask flex jc-center ai-center ta-center">
        <span><i class="icon-loading"></i><br>{{ language.tip_loading }}...</span>
      </div>

    </div>
  </div>
</template>

<script>
import MTabs from "@/components/mTabs";
import {mapGetters, mapState} from 'vuex';
import {blockConfig} from "@/common/walletConfig";
import MButton from "@/components/mButton";
import { encodeDefxCallName, getDefxCallType, BN } from "@/common/util";
import upImage from '@/images/commons/up.png'
import downImage from '@/images/commons/down.png'
import {coinsTabs} from '../americanOption/datas'

export default {
  name: "optionRecord",
  components: {MButton, MTabs},
  computed: {
     ...mapState("app", ["locale"]),
    ...mapGetters(['language']),
    ...mapState('app', ['address', 'device','getOptionList']),
    ...mapState('option', ['currentRounds', 'contractName']),
    ...mapState('aoption', ['options']),
  },
  watch: {
    getOptionList(index){
        if(index == 1){
              clearInterval(this.timer);// 清除定时器
              this.timer = null;  
              this.getTrade();
          }else{
              this.getList();
              this.timer =  setInterval(() => {
                    this.getList();
              }, 1000);
          }
    },
    currentRounds() {
      this.getList();
    },
     locale(newName) {
      if (
        newName == "de_DE" ||
        newName == "fr" ||
        newName == "es" ||
        newName == "en"
      ) {
        this.listeningLanguage = true;
      } else {
        this.listeningLanguage = false;
      }
    },
     
  },
  data() {
    return {
      timer:null,
      statusOption:null,
      listeningLanguage:false,
      upImage,
      downImage,
      tabIndex: 0,
      tabs: [
        {type: 0, language_key: 'aoption_holding_trade'},
        {type: 1, language_key: 'aoption_history_trade'},
      ],
      transactionList: [],
      loading: false, //加载中
      openFlag: {},
      roundMap: {
        '5': '5M',
        '15': '15M'
      },
      coinPrices: (() => {
        const resval = {}
        coinsTabs.forEach(item => {
          resval[item.symbol] = {
            price: new BN(0),
            decimals: 8,
          }
        })
        return resval
      })(),
    }
  },
  methods: {
    
    getCoinPrice(priceContractName, coin) {
      this.$api.getLatestPrice(priceContractName).then(res=>{
        this.coinPrices[coin].price = new BN(res.answer)
        this.coinPrices[coin].decimals = Number(res.decimals)
        console.log(this.coinPrices)
      })
    },
    displayEvenPrive(strike, direction, amount, optionPrice) {
      // 看涨： 打平价格 = 行权价格 +（权利金 /  买入数量）
      // 看跌： 打平价格 = 行权价格 - （权利金 /  买入数量）
      // const value = new BN(amount).toWei('balance').toString(10)
    
      if (direction === 1) {
        return new BN(strike).toDisplay().plus(new BN(optionPrice).div(new BN(amount).toWei('balance').toString(10))).myFixed()
      }
      return new BN(strike).toDisplay().minus(new BN(optionPrice).div(new BN(amount).toWei('balance').toString(10))).myFixed()
    },
    displayProfit(coin, direction, strike, amount) {
     
      // 看涨收益: (当前价格-行权价格)* 数量 - 期权成本
      // 看跌收益: (行权价格 - 当前价格)* 数量 - 期权成本
      // 看涨预计收益:
      // if(当前价格-行权价格 > 0) {
      // amount * （当前价格-行权价格）
      // }

      // 看跌预计收益:
      // if(行权价格 - 当前价格 > 0) {
      // amount * （行权价格 - 当前价格)
      // }

      const coinPrice = this.coinPrices[coin].price
     
      // direction： 1: 看涨 0: 看跌
      if (direction === 1) { // 看涨
        const price = coinPrice.minus(strike)
        if (price.isLessThanOrEqualTo(0)) {
          return '0'
        }
        const upPrice = price.times(amount).toDisplay()
        return new BN(upPrice).toFixed(6)
      }
      const price = new BN(strike).minus(coinPrice)
      if (price.isLessThanOrEqualTo(0)) {
        return '0'
      }
       const downPrice = price.times(amount).toDisplay()
      return new BN(downPrice).toFixed(6)
      //return this.price.minus(this.dispalyPrice).times(this.amount).minus(this.optionPrice)
    },
    getCountdownTime(hedged, timestamp, direction, symbol) {
      let endTime = 0
      const contractName = encodeDefxCallName(getDefxCallType(direction), symbol)
      // console.log(this.options[contractName], this.options, contractName)
      // 对冲结束时间
      if (hedged) {
        endTime = Number(timestamp)  + Number(this.options[contractName].lockupPeriodForHedgedTranches)
      } else { // 非对冲结束时间
        endTime = Number(timestamp)  + Number(this.options[contractName].lockupPeriodForUnhedgedTranches)
      }
      const nowTime = new Date().getTime()
      const countdown = endTime * 1000 - nowTime
      return countdown
    },
    getList() {
      if (this.loading) return;
      this.tabIndex = 0
      // this.loading = true;
      this.$api.getFacadeHistoryList(this.address, 1).then(({ options }) => {
        // this.loading = false;
        const nowTime = new Date().getTime()
        this.transactionList = options.map(item => {
        const countdownTime = Number(item.expiredtime) * 1000 - nowTime
        const isEnd  = (countdownTime >= 0);
        let displayCountdown = "";

        if(isEnd){
          displayCountdown =  new Date(countdownTime).countdown('dd{day},hh:mm:ss').replace('{day}', this.language.d)
           let str1 =  displayCountdown.split(",")[0]
           let str2 =  displayCountdown.split(",")[1]
           let st = str1.substr(1)
           displayCountdown = st+str2
      }else{
          displayCountdown =  new Date(countdownTime).countdown('dd{day}').replace('{day}', this.language.d)
          displayCountdown = displayCountdown ? displayCountdown.split("-")[1]:0
        }
         const displayTimestamp = new Date(Number(item.timestamp) * 1000).format('yyyy-MM-dd') 
          const drawPrice = '0'
          return {
            countdownTime,
            ...item,
            displayCountdown,
            isEnd,
            displayTimestamp,
            drawPrice,
            displayStrike: new BN(item.strike).toDisplay().myFixed(),
            PL: 0, // new BN(item.strike).minus(item.total).toDisplay().myFixed()
          }
        });
      }).catch((error) => {
        console.error(error)
        // this.loading = false;
      })
    },
 
  getTrade(){
     if (this.loading) return;
      this.tabIndex = 1
      this.loading = true;
      this.$api.getFacadeHistoryTrade(this.address, 1).then(({ options }) => {
        this.loading = false;
        const nowTime = new Date().getTime()
        this.transactionList = options.map(item => {
        const countdownTime = Number(item.expiredtime) * 1000 - nowTime
        const isEnd = (countdownTime >= 0)
        const displayType =  isEnd ? '00:00' : new Date(countdownTime).countdown('dd{day}').replace('{day}', this.language.d)
        const displayCountdown = displayType? displayType.split("-")[1]:0
        const displayTimestamp = new Date(Number(item.timestamp) * 1000).format('yyyy-MM-dd') 
          const drawPrice = '0'
          return {
            ...item,
            displayCountdown,
            isEnd,
            displayTimestamp,
            drawPrice,
            displayStrike: new BN(item.strike).toDisplay().myFixed(),
            PL: 0, // new BN(item.strike).minus(item.total).toDisplay().myFixed()
          }
        });
      }).catch((error) => {
        console.error(error)
        this.loading = false;
      })
    },

    // tabChangeHandle() {
    //    if(!this.tabIndex){
    //        this.getList();
    //    }else{
    //        this.getTrade();
    //    }
    // },
    claim(roundId, $el) {
      $el.setLoading(true);
      this.$api.exerciseOptionFacade(roundId).then(() => {
        $el.setLoading(false);
        this.$api.getBalance();
        this.deleteRow(roundId);
      }).catch(() => {
        $el.setLoading(false);
      })
    },
    exerciseOptionFacade(optionId, direction, symbol, btmEl) {
      btmEl.setLoading(true)
      const contractName = encodeDefxCallName(getDefxCallType(direction), symbol)
      this.$api.exerciseOptionFacade(optionId, contractName).then(() => {
        this.$toast.success(this.language.aoption_success)
        this.getList();
      }).catch((err) => {
        console.log(err)
        if (err?.code !== 4001) {
          this.$toast.error(this.language.aoption_fail)
        }
      }).finally(() => {
        btmEl.setLoading(false)
      })
    },
    deleteRow(epoch) {
      let data = this.data;
      let idx = data.findIndex(({round}) => round.epoch === epoch);
      idx !== -1 && data.splice(idx, 1);
    },
    openBlock(hash) {
      window.open(blockConfig.host + hash);
    },
  },
  beforeMount() {
     if(this.getOptionList == 1){
            clearInterval(this.timer);// 清除定时器
            this.timer = null;  
            this.getTrade();
          }else{
            this.getList();
            this.timer =  setInterval(() => {
                  this.getList();
            }, 1000);
       }

    this.getCoinPrice(coinsTabs[0].priceContractName, coinsTabs[0].symbol)
    let newName = this.locale;
    if (
      newName == "de_DE" ||
      newName == "fr" ||
      newName == "es" ||
      newName == "en"
    ) {
      this.listeningLanguage = true;
    }
  },
   beforeUnmount(){
      clearInterval(this.timer);// 清除定时器
      this.timer = null;
   },
}
</script>

